<template>
	<div id="send-gift-list">
		<commonHeader :title="'选择礼物'"></commonHeader>
		<div class="gift__tab">
      <span data-id='1' class="tab" :class="{'active': active == 1}" @click="changeType(1)">实物礼物</span>
      <span data-id='2' class="tab" :class="{'active': active == 2}" @click="changeType(2)">定制礼物</span>
      <span data-id='3' class="tab" :class="{'active': active == 3}" @click="changeType(3)">虚拟礼物</span>
    </div>
    
    <div class="gift virtual" v-for="(item,index) in data" :key="index">
      <div class="gift__check">
        <input class="radio" type="radio" name="address"  @click="checked(index)"/>
      </div>
      <div class="gift__left">
        <img src="../../assets/pic.png" alt="">
      </div>
      <div class="gift__right">
        <div class="gift__right__name">酷美人 夜光石手链 男女款萤石手222链 手串手环饰品...</div>
        
        <div class="gift__right__bottom">
          <div class="bottom__price" v-if="active != '3'">￥180</div>
          <div class="bottom__info">
            <span class="user"  v-if="active != '3'">赠送者：李四</span>
            <span class="heart" v-if="active == '3'">
              <span class="heart__icon"></span>
              <span class="heart__num">2</span>
            </span>
            <span class="num">x1</span>
          </div>
        </div>
      </div>
    </div>
    
    
    
    
    
    
    <div class="gift__bottom">
      <div class="count">
        <span class="count__text">数量</span>
        <Counter :num="value" :max="5" @change="valChange"/>
      </div>
      <div class="submit">确定</div>
    </div>
	</div>
</template>

<script>
import Counter from '@/components/Counter/Counter';
export default {
	data() {
      return {
        active: '1',
        value: 1,
        data:[{},{},{}]
      }
    },

    methods: {
      changeType(type) {
        this.active = type;
      },

      //计算器
      valChange(val) {
        this.value += val;
     	},

      //当type = 3， 点击选中返回选中下标
      getCheckedIndex(index) {
        console.log(index)
      },
      //选中并返回选中下标
      checked(index) {
        this.$emit('checked', index);
      }
    },
    
	  components: {
	      Counter,
	  },
  
}
</script>

<style lang="scss">
	@import "../../sass/baseSass.scss";
	#send-gift-list{
		.gift__tab {
      height: 1.067rem;
      line-height: 1.067rem;
      font-size: 0;
      background: #fff;
      border-bottom: 1px solid #eee;
      .tab {
        display: inline-block;
        width: 33.3%;
        text-align: center;
        font-size: 0.4rem;
        color: #333;
      }
      .tab.active {
        position: relative;
        color: #de7fef;

        &:after {
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          height: 2px;
          width: 100%;
          background: #de7fef;
        }
      }
    }
    
    .gift {
      padding: 0.267rem 0.4rem;
      height: 2.9333rem;
      background: #fff;
      box-sizing: content-box;
      border-bottom: 1px solid #eee;

      .gift__left {
        float: left;

        img {
          width: 2.9333rem;
          height: 2.9333rem;
        }
      }

      .gift__right {
        position: relative;
        float: right;
        width: 5.867rem;
        height: 2.9333rem;

        .gift__right__name {
          font-size: 0.3733rem;
        }

        .gift__right__bottom {
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;

          .bottom__price {
            font-size: 0.453rem;
            font-weight: 600;
            color: #fe4436;
          }

          .bottom__info {
            position: relative;
            width: 100%;
            color: #666;

            span {
              display: inline-block;
            }

            .num {
              position: absolute;
              right: 0;
              bottom: 0;
              vertical-align: middle;
            }

            .heart {
              vertical-align: middle;
              .heart__icon {
                display: inline-block;
                width: 0.5067rem;
                height: 0.45333rem;
                background: url('../../assets/heart.png') no-repeat;
                background-size: 100%;
                background-position: center;
                vertical-align: middle;
              }

              .heart__num {
                color: #de7fef;
                vertical-align: middle;
              }
            }
          }
        }
      }
    }

    .virtual {
      .gift__right {
        width: 5.267rem;
      }

      .gift__check {
        float: left;
        display: flex;
        height: 100%;
        align-items: center;

        .radio{
          position: relative;
          -moz-appearance: none;
          -webkit-appearance: none;
          float: left;
          margin-right: 0.2rem;
          width: 0.533rem;
          height: 0.533rem;

          &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 0.533rem;
            height: 0.533rem;
            background: #fff;
            border: 1px solid #eee;
            border-radius: 50%;
            z-index: 9;
            box-sizing: border-box;
          }

          &:checked {
            &::after {
              border: none;
              background: url("../../assets/cart/ico_checked.png") no-repeat center;
              background-size: 100%;
            }
          }
        }

      }
    }
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    
    .gift__bottom {
      position: fixed;
      bottom: 0;
      left: 0;
      display: flex;
      justify-content: space-between;
      height: 1.3067rem;
      line-height: 1.3067rem;
      width: 100%;
      font-size: 0.453rem;
      background: #fff;
      border-top: 1px solid #eee;

      .count {
        display: flex;

        .count__text {
          padding: 0 0.6rem 0 0.4rem;
        }
      }

      .submit {
        padding: 0 1.07rem;
        color: #fff;
        background-image: linear-gradient(135deg, #fe9bba 0%, #de7fef 100%);
      }
    }
	}
</style>


